"use client"

import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import MenuItem from '@mui/material/MenuItem';
import LanguageIcon from '@mui/icons-material/Language';
import PersonIcon from '@mui/icons-material/Person';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';

const pages = ['Flights', 'Hotels', 'Tours', 'Cars', 'Visa', 'Blogs'];

function TravelNavbar() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);
  const [anchorElLang, setAnchorElLang] = React.useState(null);
  const [anchorElCurrency, setAnchorElCurrency] = React.useState(null);
  const [anchorElAgents, setAnchorElAgents] = React.useState(null);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
  
  const handleOpenLangMenu = (event) => {
    setAnchorElLang(event.currentTarget);
  };
  
  const handleOpenCurrencyMenu = (event) => {
    setAnchorElCurrency(event.currentTarget);
  };
  
  const handleOpenAgentsMenu = (event) => {
    setAnchorElAgents(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseLangMenu = () => {
    setAnchorElLang(null);
  };

  const handleCloseCurrencyMenu = () => {
    setAnchorElCurrency(null);
  };

  const handleCloseAgentsMenu = () => {
    setAnchorElAgents(null);
  };

  return (
    <AppBar 
      position="static" 
      sx={{ 
        backgroundColor: 'white', 
        color: 'black',
        boxShadow: 'none',
        borderBottom: '1px solid #e0e0e0'
      }}
    >
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          {/* Logo */}
          <Typography
            variant="h6"
            noWrap
            component="a"
            href="#"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontWeight: 700,
              color: '#1a237e',
              textDecoration: 'none',
              fontSize: '1.5rem'
            }}
          >
            PHPTRAVELS
          </Typography>

          {/* Mobile menu */}
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
              sx={{ color: 'black' }}
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{ display: { xs: 'block', md: 'none' } }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography sx={{ textAlign: 'center' }}>{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          
          {/* Mobile logo */}
          <Typography
            variant="h5"
            noWrap
            component="a"
            href="#"
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontWeight: 700,
              color: '#1a237e',
              textDecoration: 'none',
            }}
          >
            PHPTRAVELS
          </Typography>
          
          {/* Desktop menu items */}
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, ml: 4 }}>
            {pages.map((page) => (
              <Button
                key={page}
                onClick={handleCloseNavMenu}
                sx={{ my: 2, color: '#555', display: 'block', mx: 1 }}
              >
                {page}
              </Button>
            ))}
          </Box>
          
          {/* Right side controls */}
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            {/* Travel Tech Partner */}
            <Typography
              variant="body2"
              sx={{
                display: { xs: 'none', lg: 'block' },
                color: '#666',
                fontStyle: 'italic',
                mr: 2
              }}
            >
              Travel Tech Partner
            </Typography>
            
            {/* Language */}
            <Button
              onClick={handleOpenLangMenu}
              startIcon={<LanguageIcon />}
              endIcon={<ArrowDropDownIcon />}
              sx={{ color: '#555', textTransform: 'none', mr: 1 }}
            >
              English
            </Button>
            <Menu
              anchorEl={anchorElLang}
              open={Boolean(anchorElLang)}
              onClose={handleCloseLangMenu}
            >
              <MenuItem onClick={handleCloseLangMenu}>English</MenuItem>
              <MenuItem onClick={handleCloseLangMenu}>Spanish</MenuItem>
              <MenuItem onClick={handleCloseLangMenu}>French</MenuItem>
            </Menu>
            
            {/* Currency */}
            <Button
              onClick={handleOpenCurrencyMenu}
              endIcon={<ArrowDropDownIcon />}
              sx={{ color: '#555', textTransform: 'none', mr: 1 }}
            >
              USD
            </Button>
            <Menu
              anchorEl={anchorElCurrency}
              open={Boolean(anchorElCurrency)}
              onClose={handleCloseCurrencyMenu}
            >
              <MenuItem onClick={handleCloseCurrencyMenu}>USD</MenuItem>
              <MenuItem onClick={handleCloseCurrencyMenu}>EUR</MenuItem>
              <MenuItem onClick={handleCloseCurrencyMenu}>GBP</MenuItem>
            </Menu>
            
            {/* Agents */}
            <Button
              onClick={handleOpenAgentsMenu}
              startIcon={<PersonIcon />}
              sx={{ color: '#555', textTransform: 'none' }}
            >
              Agents
            </Button>
            <Menu
              anchorEl={anchorElAgents}
              open={Boolean(anchorElAgents)}
              onClose={handleCloseAgentsMenu}
            >
              <MenuItem onClick={handleCloseAgentsMenu}>Agent Login</MenuItem>
              <MenuItem onClick={handleCloseAgentsMenu}>Agent Registration</MenuItem>
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}

export default TravelNavbar;